import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import Slide from '@material-ui/core/Slide';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { push } from 'react-router-redux';

import './styles.css';
import _ from 'lodash';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import { store } from '../../../store';
import Product from './Product';
import {
  REMOVE_FROM_CART, ADD_TO_CART,
} from '../../../constants/actionTypes';


const Transition = React.forwardRef((props, ref) => <Slide direction="up" ref={ref} {...props} />);

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      open: false,
      user: {},
    };
    this.cartItems = [];
  }

  componentWillMount() {
    if (this.props.cartItems) {
      this.cartItems = this.props.cartItems;
      this.setState({ render: !this.state.render });
    }
    if (this.props.publicPageUser) {
      this.setState({ user: this.props.publicPageUser });
    }
  }

  componentWillReceiveProps(np) {
    if (np.data && np.data !== this.state.product) {
      this.setState({ product: np.data });
    }
    if (np.publicPageUser) {
      this.setState({ user: np.publicPageUser });
    }
    if (np.cartItems) {
      this.cartItems = np.cartItems;
      this.setState({ render: !this.state.render });
    }
  }

  modifyQuantity(product, change) {
    const changeQuntity = product.quantity + change;
    if (changeQuntity > 0) {
      const temp = { ...product, quantity: change };
      this.props.addToCart(temp);
    }
  }

  togelDialog() {
    this.setState({ open: !this.state.open });
  }

  renderCartContents() {
    let totalQuantity = 0;
    let totalPrice = 0;
    let deliveryCharges = 0;
    return (
      <div>
        <div className="cart-details-cart-item total">
          <h3>Product</h3>
          <h3 className="quantity-div">Quantity</h3>
          <h3>Price</h3>

        </div>
        {
            this.cartItems.map((c, i) => {
              totalPrice += (c.quantity * c.item.disc_price);
              totalQuantity += c.quantity;
              let header = `${c.item.name}`;
              if (c.selectedSize) {
                header = `${header} ( ${c.selectedSize} )`;
              }
              deliveryCharges = Math.max(deliveryCharges, c.item.shipping_charges);
              return (
                <div className="cart-details-cart-item" key={c.item.id}>
                  <h3>{header}</h3>
                  <div className="flex quantity-div">

                    <input type="number " value={c.quantity} className="tc" disabled />
                    <div className="flex flex-column w-40">
                      <button type="button" onClick={() => this.modifyQuantity(c, 1)}>
                        {' '}
                        <ExpandLessIcon />
                        {' '}
                      </button>
                      <button type="button" onClick={() => this.modifyQuantity(c, -1)}>
                        {' '}
                        <ExpandMoreIcon />
                        {' '}
                      </button>
                    </div>
                  </div>

                  <h3>{c.item.disc_price}</h3>

                </div>
              );
            })
            }
            <div className="cart-details-cart-item total" >
              <h4>Shipping Charges: </h4>
              <h4> </h4>
              <h4>{`${deliveryCharges} Rs.`} </h4>
            </div>
        <div className="cart-details-cart-item total">
          <h3>Total</h3>
          <h3 className="quantity-div">{totalQuantity}</h3>
          <h3>{totalPrice+deliveryCharges}</h3>

        </div>
      </div>

    );
  }

  render() {
    const {
      open, user,
    } = this.state;

    return (
      <div className="">

        <button className="hidden-btn" onClick={this.togelDialog.bind(this)}>
            open cart item
        </button>

        <Dialog fullScreen open={open} onClose={this.togelDialog.bind(this)} TransitionComponent={Transition}>
          <div className="cart-details-top-bar">
            <h2>My Cart</h2>
            <button type="button" onClick={this.togelDialog.bind(this)}>
                  Close
            </button>
          </div>
          <div className="cart-details-cart-content" style={{ justifyContent: 'space-evenly' }}>
            <div className="cart-details-content-image">
              <ShoppingCartIcon />
              <h2>Cart Content</h2>
            </div>
            {
                this.cartItems.length === 0
                  ? <p className="tc">No items in cart Purchase some Items</p>

                  : (
                    <div>
                      <div style={{ justifyContent: 'space-evenly', display: 'flex', flexWrap: 'wrap ' }}>

                        {
                          this.cartItems.map((p, i) => (
                            <Product data={p.item} key={i} cartItem />
                          ))
                        }
                      </div>
                      <div className="cart-details-cart-table">
                        {
                          this.renderCartContents()
                        }
                      </div>
                      <DialogActions>
                        <button className="color-btn cart-purchase-btn" onClick={() => (store.dispatch(push('/user-payment/')))} type="button"> Checkout</button>
                      </DialogActions>
                    </div>
                  )
            }


          </div>

        </Dialog>

      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  cartItems: state.HomePageReducers.cartItems,
  publicPageUser: state.HomePageReducers.publicPageUser,


});

const mapDispatchToProps = (dispatch) => ({
  addToCart: (product) => dispatch({ type: ADD_TO_CART, payload: product }),
  removeFromCart: (product) => dispatch({ type: REMOVE_FROM_CART, payload: product }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
