
import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import { Carousel } from 'react-responsive-carousel';
import Slide from '@material-ui/core/Slide';
import CartDetails from './CartDetails';
import './styles.css';
import Image from '../../../common/Components/Image';
import ZoomImage from '../../../common/Components/ZoomImage';

import {
  ADD_TO_CART, REMOVE_FROM_CART,
} from '../../../constants/actionTypes';

const Transition = React.forwardRef((props, ref) => <Slide direction="up" ref={ref} {...props} />);

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isCreateProductDialog: false,
      alreadyInCart: false,
      product: { images: [] },
      quantity: 1,
      sizes: [],
      isSizesAvailable: false,
      selectedSize: '',
    };
    this.cartItems = [];
    this.togelIsCreateProductDialog = this.togelIsCreateProductDialog.bind(this);
    this.togelIsZoomImageDialog = this.togelIsZoomImageDialog.bind(this);
  }

  componentWillMount() {
    if (this.props.data) {
      this.setState({ product: this.props.data });
      const { data } = this.props;
      if (data.meta_data && data.meta_data.sizes_available) {
        this.setState({ sizes: data.meta_data.sizes_available, isSizesAvailable: data.meta_data.sizes_available.length > 0 });
      }
    }
    if (this.props.cartItems) {
      this.cartItems = this.props.cartItems;
      const temp = this.props.cartItems.filter((c) => c.item.id === this.props.data.id);
      if (temp.length > 0) {
        this.setState({ alreadyInCart: true, quantity: temp[0].quantity, selectedSize: temp[0].selectSize });
      } else {
        this.setState({ alreadyInCart: false, quantity: 1, selectedSize: '' });
      }
    }
  }

  componentWillReceiveProps(np) {
    if (np.data) {
      this.setState({ product: np.data });
      const data = np;
      if (data.meta_data && data.meta_data.sizes_available) {
        this.setState({ sizes: data.meta_data.sizes_available, isSizesAvailable: data.meta_data.sizes_available.length > 0 });
      }
    }

    if (np.cartItems) {
      this.cartItems = np.cartItems;
      const temp = np.cartItems.filter((c) => c.item.id === np.data.id);
      if (temp.length > 0) {
        this.setState({ alreadyInCart: true, quantity: temp[0].quantity, selectedSize: temp[0].selectSize });
      } else {
        this.setState({ alreadyInCart: false, quantity: 1, selectedSize: '' });
      }
    }
  }

  togelIsCreateProductDialog() {
    this.setState({ isCreateProductDialog: !this.state.isCreateProductDialog });
  }

  togelIsZoomImageDialog() {
    this.setState({ isZoomImageDialogOpen: !this.state.isZoomImageDialogOpen });
  }

  addToCart() {
    const {
      isSizesAvailable, selectedSize, product, quantity,
    } = this.state;
    if (isSizesAvailable && selectedSize === '') {
      this.setState({ showSizes: true });
    } else {
      const data = {
        item: product,
        quantity,
        selectedSize,
      };
      this.props.addToCart(data);
      this.setState({ showSizes: false });
    }
  }

  removeProduct() {
    this.props.removeFromCart(this.state.product);
  }

  modifyQuantity(change) {
    if (this.state.quantity + change > 0) this.setState({ quantity: this.state.quantity + change });
  }

  selectSize(e) {
    this.setState({ selectedSize: e.target.value });
  }

  renderZoomImageDialog() {
    const { isZoomImageDialogOpen, product } = this.state;
    return (
      <Dialog
        open={isZoomImageDialogOpen}
        TransitionComponent={Transition}
        keepMounted
        fullWidth
        name="zoomImageDialog"
        onClose={this.togelIsZoomImageDialog.bind(this)}
      >
        <div className='tc b f5' >
          <p>
          Double click or pinch to zoom.
          </p>
        </div>
        <Carousel background="#000" animation	='fade' showThumbs>
          { product.images.map((img, i) => (
            <ZoomImage key={i}>
              <img src={img} alt="" overlaySrc={product.preview_images[0]} key={i} />
            </ZoomImage>
          ))}
        </Carousel>
        
        <DialogActions>
          <button type='button' className='color-btn w-50' onClick={this.togelIsZoomImageDialog} > close</button> 
        </DialogActions>
      </Dialog>
    );
  }

  render() {
    const {
      name, images, price, disc_price, description, id, estimated_delivery, preview_images, cod_available,
    } = this.state.product;
    const {
      alreadyInCart, isCreateProductDialog, quantity, showSizes,
    } = this.state;
    return (
      <div>

        <button
          className=" center  flex hidden-btn "
          type="button"
          onClick={this.togelIsCreateProductDialog.bind(this)}
        >
              see Product details
        </button>

        <Dialog
          open={isCreateProductDialog}
          TransitionComponent={Transition}
          keepMounted
          fullScreen
          onClose={this.togelIsCreateProductDialog.bind(this)}
        >
          <div className="cart-details-top-bar">
            <h2>{name}</h2>
            <div className="flex items-center justify-between">
              <button className="home-page-cart-div items-center ju flex relative ml1 mr1" type="button">
                <ShoppingCartIcon style={{ color: 'white' }} />
                <CartDetails />
                <span style={{ color: 'white', fontWeight: 'bold' }}>
                  {`Cart(${this.cartItems.length})`}
                </span>
              </button>
              <button type="button" onClick={this.togelIsCreateProductDialog.bind(this)}>
                  Close
              </button>
            </div>
          </div>

          <DialogContent>
            <div className="product-details">
              <div className="product-details-top-div">
                <Carousel className="product-details-carousel" background="#000" onClickItem={this.togelIsZoomImageDialog.bind(this)} showThumbs={false} >
                  { images.map((img, i) => (
                    <Image src={img} alt="" overlaySrc={preview_images[0]} key={i} />
                  ))}
                </Carousel>

                <div className="cart-details-top-right-div">
                  <h1>{name}</h1>
                  <div className="product-pricing">

                    {
                disc_price === price
                  ? (
                    <b>
                        Rs.
                      {price}
                    </b>
                  )
                  : (
                    <div>

                      <b>
                        Rs.
                        {disc_price}
                      </b>
                      <strike>{` Rs. ${price}`}</strike>
                      <span className="product-details-product-sale">
                        {Math.floor(((price - disc_price) / price) * 100)}
%off
                      </span>
                    </div>
                  )
            }
                  </div>

                  <h4>
                    {`Estimated Delivery:${estimated_delivery} Days`}
                  </h4>
                  {
                    cod_available
                      ? (
                        <p className="err-txt b">Cash on delivery available</p>
                      )
                      : null
                  }

                  <div className="product-add-to-cart-div">


                    {
                      alreadyInCart
                        ? (
                          <button type="button" className="product-add-to-cart relative">
                            View Cart
                            <CartDetails />
                          </button>
                        )
                        : (
                          <div>
                            <div className="flex flex-column mt1 mb1">
                              { this.state.sizes.length > 0
                              && (
                              <select onChange={this.selectSize.bind(this)}>
                                <option value={null}>Select Size</option>

                                {
                                  this.state.sizes.map((s, i) => (
                                    <option value={s} key={i}>{s}</option>
                                  ))
                                }
                              </select>
                              )}
                              {
                                showSizes ? <p className="err-txt"> *Select a size </p> : null
                              }
                            </div>
                            <button
                              type="button"
                              className="product-add-to-cart"
                              onClick={this.addToCart.bind(this)}

                            >
                              Add To Cart
                            </button>
                          </div>
                        )
                    }

                    {
                    alreadyInCart
                      ? (

                        <div className="f6 go-to-cart relative">
                        Item already in cart.
                        </div>
                      )
                      : null
                  }
                  </div>
                </div>

              </div>
              <div style={{ overflowWrap: 'anywhere' }}>
                <h4> Description : </h4>
                <p>{description}</p>
              </div>
            </div>
          </DialogContent>

          {
                  this.renderZoomImageDialog()
                }
        </Dialog>
      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  cartItems: state.HomePageReducers.cartItems,

});

const mapDispatchToProps = (dispatch) => ({
  addToCart: (product) => dispatch({ type: ADD_TO_CART, payload: product }),
  removeFromCart: (product) => dispatch({ type: REMOVE_FROM_CART, payload: product }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
