import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { push } from 'react-router-redux';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import { loadStripe } from '@stripe/stripe-js';

import { toast } from 'react-toastify';
import './styles.css';
import _ from 'lodash';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import { store } from '../../../store';
import agent from '../../../agent';
import Loader from '../../../common/Components/Loader';


import CartDetails from './CartDetails';
import TextInput from '../../../common/Components/TextInput';

const Transition = React.forwardRef((props, ref) => <Slide direction="up" ref={ref} {...props} />);

const state = ['Andhra Pradesh',
  'Arunachal Pradesh',
  'Assam',
  'Bihar',
  'Chhattisgarh',
  'Goa',
  'Gujarat',
  'Haryana',
  'Himachal Pradesh',
  'Jammu and Kashmir',
  'Jharkhand',
  'Karnataka',
  'Kerala',
  'Madhya Pradesh',
  'Maharashtra',
  'Manipur',
  'Meghalaya',
  'Mizoram',
  'Nagaland',
  'Odisha',
  'Punjab',
  'Rajasthan',
  'Sikkim',
  'Tamil Nadu',
  'Telangana',
  'Tripura',
  'Uttarakhand',
  'Uttar Pradesh',
  'West Bengal',
  'Andaman and Nicobar Islands',
  'Chandigarh',
  'Dadra and Nagar Haveli',
  'Daman and Diu',
  'Delhi',
  'Lakshadweep',
  'Puducherry'];

// Make sure to call `loadStripe` outside of a component’s render to avoid
// recreating the `Stripe` object on every render.
// const stripePromise = loadStripe('pk_live_vBJsiLAJ0R6oih9AMt19015u00R99pLI26');
class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      open: false,
      user: {},
      userInfo: { country: 'India', state: 'Andhra Pradesh' },
      totalPrice: 0,
      isConfirmOrderModelOpen: false,
      isCodEligible: true,
      codItems: [],
      onlineItems: [],
      deliveryCharges:0,
      // userName: '',
    };
    this.cartItems = [];
    this.cod = false;
    this.togelConfirmOrderModal = this.togelConfirmOrderModal.bind(this);
  }

  componentWillMount() {
    if (this.props.cartItems) {
      this.cartItems = this.props.cartItems;
      this.checkCashOndelivery();
      this.calculateTotal();
      this.setState({ render: !this.state.render });
    }

    if (this.props.publicPageUser) {
      this.setState({ user: this.props.publicPageUser });
    }
  }

  componentWillReceiveProps(np) {
    if (np.publicPageUser) {
      this.setState({ user: np.publicPageUser });
    }
    if (np.cartItems) {
      this.cartItems = np.cartItems;
      this.calculateTotal();
      this.setState({ render: !this.state.render });
      this.checkCashOndelivery();
    }
  }

  checkCashOndelivery(cartItems) {
    this.cartItems.forEach((c) => {
      if (!c.item.cod_available) {
        this.setState({ isCodEligible: false });
      }
    });
  }

  togelConfirmOrderModal(paymentMode) {
    this.paymentMode = paymentMode;
    if (paymentMode === 'online') {
      const codItems = this.cartItems.filter((c) => ((c.item.online_available === false && c.item.cod_available)));
      const onlineItems = this.cartItems.filter((c) => ((c.item.online_available)));
      this.setState({ codItems, onlineItems });
    } else if (paymentMode === 'cod') {
      const onlineItems = this.cartItems.filter((c) => ((c.item.online_available && c.item.cod_available === false)));
      const codItems = this.cartItems.filter((c) => ((c.item.cod_available)));
      this.onlineItems = onlineItems  ;
      
      this.setState({ onlineItems, codItems });
    }
    this.setState({ isConfirmOrderModelOpen: !this.state.isConfirmOrderModelOpen });
    // this.cod = false;
  }

  calculateTotal() {
    let totalPrice = 0;
    let deliveryCharges = 0;
    // const { deliveryCharges } = this.state;
    this.cartItems.map((c) => {
      totalPrice += (c.quantity * c.item.disc_price);
      deliveryCharges = Math.max(deliveryCharges,c.item.shipping_charges);
    });
    totalPrice += deliveryCharges;
    this.setState({ totalPrice, deliveryCharges });
  }

  purchaseCart(e) {
    if (e) {
      e.preventDefault();
      console.log(e);
    }
    const { onlineItems, codItems, userInfo } = this.state;
    this.setState({ loading: true });
    const tempOnlineItems = onlineItems.map((p) => ({
      type: 'product',
      id: p.item.id,
      meta_data: {
        size: p.selectedSize,
        quantity: p.quantity,
      },
    }));
    const tempCodItems = codItems.map((p) => ({
      type: 'product',
      id: p.item.id,
      meta_data: {
        size: p.selectedSize,
        quantity: p.quantity,
      },
    }));
    const data = {
      user_details: userInfo,
      online_items: tempOnlineItems,
      cod_items: tempCodItems,
    };
    console.log(data);
    agent.ProductsPage.makeOrder(data).then(async (res) => {
      // console.log(res.data);
      if (res.data.form) {
        const payment = res.data.form;
        const form = document.createElement('form');
        form.action = payment.url;
        form.method = 'POST';
        for (const field in payment.fields) {
          const input = document.createElement('input');
          input.setAttribute('hidden', true);
          input.setAttribute('name', field);
          input.setAttribute('value', payment.fields[field]);
          form.appendChild(input);
        }
        document.body.appendChild(form);
        form.submit();
        // const stripe = await stripePromise;
        // const { error } = await stripe.redirectToCheckout({
        //   sessionId: res.data.id,
        // });
        // console.log('error ocurred in the stripe payment : ', error);
        // // If `redirectToCheckout` fails due to a browser or network
        // // error, display the localized error message to your customer
        // // using `error.message`.
      } else if (res.data.message === 'Order received') {
        store.dispatch(push('/user-payment/success'));
      } else {
        store.dispatch(push('/user-payment/success'));
      }
      this.setState({ loading: false });
    }).catch((err) => {
      console.log(err, err.response);
      toast.error('An error Occurred!!!');
      this.setState({ loading: false });
    });
  }

  confirmOrder() {
    this.cod = true;
    const button = document.createElement('button');
    button.setAttribute('form', 'payment-info-form');
    button.setAttribute('type', 'submit');
    button.setAttribute('hidden', true);

    document.body.appendChild(button);
    button.click();
  }

  render() {
    const {
      open, user, userInfo, totalPrice, isConfirmOrderModelOpen, deliveryCharges, onlineItems, codItems,
    } = this.state;
    let codTotal = 0;
    let onlineTotal = this.paymentMode ==='cod' ? deliveryCharges :0;
    let totalQuantity = 0;
    return (
      <div className="">
        <div className="cart-details-top-bar">
          <h2>Payment Info</h2>
          <button type="button" onClick={() => store.dispatch(push(`/${user.username}`))}>
                Go to Home
          </button>
        </div>
        <div className="" style={{ justifyContent: 'space-evenly' }}>
          <div className="payment-form-header">
            <h2>Enter Your Details</h2>
            <button className="relative cart-btn">
              <CartDetails />
              <ShoppingCartIcon />
              <p> Your cart </p>
            </button>
          </div>
          <form id="payment-info-form" onSubmit={this.purchaseCart.bind(this)}>
            <h3>Contact Information</h3>

            <TextInput label="Email" value={userInfo.email} type="email" className="w-100" onTextChange={(text) => this.setState({ userInfo: { ...userInfo, email: text } })} required />
            <TextInput label="Phone number" value={userInfo.number} type="number" className="w-100" onTextChange={(text) => this.setState({ userInfo: { ...userInfo, number: text } })} required />

            <h3>Delivery Address</h3>
            <TextInput label="Customer Name" value={userInfo.name} type="text" className="w-100" onTextChange={(text) => this.setState({ userInfo: { ...userInfo, name: text } })} required />
            <TextInput label="Address" value={userInfo.address} type="text" className="w-100" onTextChange={(text) => this.setState({ userInfo: { ...userInfo, address: text } })} required />
            <TextInput label="Apartment,flat no, etc.." value={userInfo.apartment} type="text" className="w-100" onTextChange={(text) => this.setState({ userInfo: { ...userInfo, apartment: text } })} required />
            <TextInput label="City" value={userInfo.city} type="text" className="w-100" onTextChange={(text) => this.setState({ userInfo: { ...userInfo, city: text } })} required />

            <div className="payment-form-flex-input">
              <select>
                <option value="India"> India</option>
              </select>
              <select onChange={(e) => this.setState({ userInfo: { ...userInfo, state: e.target.value } })}>
                {
                    state.map((s, i) => (
                      <option value={s} key={i}>{s}</option>
                    ))
                }
              </select>
              <TextInput label="PIN code" value={userInfo.pincode} type="number" className="" style={{ flexGrow: 1 }} onTextChange={(text) => this.setState({ userInfo: { ...userInfo, pincode: text } })} required />
            </div>
            <div className="flex">
              <button type="button" className="color-btn w-100 pa3 mt2 mr2" onClick={() => this.togelConfirmOrderModal('cod')} style={{ position: 'relative' }}>
               Cash on delivery
              </button>
              <button type="button" className="color-btn w-100 pa3 mt2 ml2" onClick={() => this.togelConfirmOrderModal('online')}>
                Pay  Online
              </button>
            </div>

          </form>
        </div>
        {
              this.state.loading
                ? <Loader />
                : null
          }
        <Dialog
          open={isConfirmOrderModelOpen}
          TransitionComponent={Transition}
          keepMounted


          onClose={this.togelConfirmOrderModal.bind(this)}
        >
          <DialogTitle>Confirm Order</DialogTitle>
          <DialogContent>
            {
              this.paymentMode === 'online'
                ? (
                  <div>
                    {
                    codItems.length !== 0
                      ? (
                        <div>

                          <div className="ba ">
                            {
                              codItems.map((item, i) => {
                                totalQuantity += item.quantity;
                                codTotal += (item.quantity) * (item.item.disc_price);
                                onlineTotal = totalPrice - codTotal;
                                return (
                                  <div className="flex items-center justify-around w-100" key={i}>
                                    {
                                      item.selectedSize
                                        ? <b>{`${item.item.name}(${item.selectedSize})`}</b>
                                        : <b>{`${item.item.name}`}</b>
                                    }
                                    <b>{`${item.quantity}`}</b>
                                    <b>{`${(item.quantity) * (item.item.disc_price)}`}</b>
                                  </div>
                                );
                              })
                            }
                            <div className="flex items-center justify-around w-100 mt2 bt ">
                              <b>Total</b>
                              <b>{`${totalQuantity}`}</b>
                              <b>{`${codTotal}`}</b>
                            </div>
                          </div>
                          <p className=" tc">
                            {`This items are not eligible for online payment You have to pay  `}<b className='red' >{`Rs. ${codTotal} on delivery.`}</b>
                          </p>
                        </div>
                      )
                      : (
                        <div>
                          <p className="tc"> On confirming, you will be redirected to payment Gateway. </p>
                        </div>
                      )
                  }

                  </div>
                )
                : (
                  <div>
                    {
                    onlineItems.length !== 0
                      ? (
                        <div>
                          <div className="ba ">
                            {
                              onlineItems.map((item, i) => {
                                totalQuantity += item.quantity;
                                onlineTotal += (item.quantity) * (item.item.disc_price);
                                codTotal = totalPrice - onlineTotal;
                                return (
                                  <div className="flex items-center justify-around w-100" key={i}>
                                    {
                                      item.selectedSize
                                        ? <b>{`${item.item.name}(${item.selectedSize})`}</b>
                                        : <b>{`${item.item.name}`}</b>
                                    }
                                    <b>{`${item.quantity}`}</b>
                                    <b>{`${(item.quantity) * (item.item.disc_price)}`}</b>
                                  </div>
                                );
                              })
                            }
                            <div className="flex items-center justify-around w-100 mt2 bt ">
                              <b>Total</b>
                              <b>{`${totalQuantity}`}</b>
                              <b>{`${onlineTotal}`}</b>
                            </div>
                          </div>
                          <p className=" tc">
                            {`This items are not eligible for Cash on Delivery. You have to pay `}
                            <b className='red' >{`Rs. ${onlineTotal} online.`}</b>
                          </p>
                        </div>
                      )
                      : (
                        <div>
                          <p className="tc">{`On confirming, you have to pay Rs. ${totalPrice} on delivery.`}</p>
                        </div>
                      )
                  }
                  </div>
                )
            }
          </DialogContent>
          <DialogActions>

            <div>
              <button className="b color-btn" onClick={() => this.confirmOrder(true)}>
                {
                    onlineItems.length !== 0
                      ? (
                        <p className='mt0 mb0' >{`Pay Rs. ${onlineTotal === 0 ? totalPrice : onlineTotal} online`}</p>
                      )
                      : (
                        <p className='mt0 mb0'>Confirm Order</p>
                      )

                  }
              </button>
            </div>
            <button className="b btn" type="button" onClick={() => this.togelConfirmOrderModal(this.paymentMode)}>
              Cancel
            </button>
          </DialogActions>
        </Dialog>

      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  cartItems: state.HomePageReducers.cartItems,
  publicPageUser: state.HomePageReducers.publicPageUser,
});

const mapDispatchToProps = (dispatch) => ({
//   addToCart: (product) => dispatch({ type: ADD_TO_CART, payload: product }),
//   removeFromCart: (product) => dispatch({ type: REMOVE_FROM_CART, payload: product }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
